export type OmitTyped<T, K extends keyof T> = Pick<T, Exclude<keyof T, K>>;

export interface ITypeHelper {
    GetObjectType(param: object): string;

    isArray(param: object): boolean;

    isString(param: any): boolean;

    isNumber(param: object): boolean;
    isJsonString(param: string): boolean;
    isNull(param: object): boolean;
    isFunction(param: object): boolean;
    isObject(param: object): boolean;
    isBool(param: object): boolean;
    isDateString(param: string): boolean;

    cloneObject(obj: any): any;
}

export class TypeHelper implements ITypeHelper {
    isJsonString(param: string): boolean {
        try {
            JSON.parse(param);
        } catch (e) {
            return false;
        }
        return true;
    }
    GetObjectType(param: object): string {
        return Object.prototype.toString.call(param);
    };
    /** @deprecated use instanceof for typescript type distinction intellisense. ex: if (variable instanceof Array)*/
    isArray(param: any): boolean {
        return this.GetObjectType(param) == "[object Array]";
    };

    isString(param: any): boolean {
        return this.GetObjectType(param) === "[object String]";
    };

    isNumber(param: any): boolean {
        return this.GetObjectType(param) === "[object Number]";
    };

    isNull(param?: any): boolean {
        return param === null || param === undefined;
    };
    isFunction(param: any): boolean {
        return this.GetObjectType(param) === "[object Function]";
    };
    isObject(param: any): boolean {
        return this.GetObjectType(param) === "[object Object]";
    };
    isBool(param: any): boolean {
        return this.GetObjectType(param) === "[object Boolean]";
    };

    isBooleanConstructor(param: any): boolean {
        return param?.toString() == "function Boolean() { [native code] }";
    }

    isStringConstructor(param: any): boolean {
        return param?.toString() == "function String() { [native code] }"
    }

    isArrayConstructor(param: any): boolean {
        return param?.toString() == "function Array() { [native code] }"
    }

    isObjectConstructor(param: any): boolean {
        return param?.toString() == "function Object() { [native code] }"
    }
    
    isNumberConstructor(param: any): boolean {
        return param?.toString() == "function Number() { [native code] }"
    }
    isDateString(param: any): boolean {
        if (!this.isString(param)) {
            return false;
        }
        return !isNaN(Date.parse(param));
    };
    isDate(param: object): boolean {
        return this.GetObjectType(param) === "[object Date]";
    }

    isFile(param: object): boolean {
        return this.GetObjectType(param) === "[object File]";
    }

    cloneObject(obj: any): any {
        var copy;

        // Handle the 3 simple types, and null or undefined
        if (null == obj || "object" != typeof obj) {
            return obj;
        }

        // Handle Date
        if (obj instanceof Date) {
            copy = new Date();
            copy.setTime(obj.getTime());
            return copy;
        }

        // Handle Array
        if (Array.isArray(obj)) {
            copy = [];
            for (var i = 0, len = obj.length; i < len; i++) {
                copy[i] = this.cloneObject(obj[i]);
            }
            return copy;
        }

        // Handle Object
        if (typeof obj === "object") {
            copy = obj.constructor && obj.constructor.length < 1 ? new obj.constructor() : {};
            for (var attr in obj) {
                if (obj.hasOwnProperty(attr)) {
                    copy[attr] = this.cloneObject(obj[attr]);
                }
            }
            return copy;
        }

        throw new Error("Unable to copy obj! Its type isn't supported.");
    };
}